export default {
  data () {
    return {}
  },
  methods: {
    onGetVideoDetailsFn (item) {
      this.$emit('getVideoDetailsChild', item.vid)
    }
  },
  props: {
    lists: {
      type: Array,
      default: []
    },
    obj: {
      type: Object,
      default: {}
    }
  }
}
